import json from "@/json.js"
export default {
	data() {
		return {
			backgroundColor: '#FFFFFF',
			selectTextColor: '#E04B28',
			defaultTextColor: '#333333',
			tabTitleDates: ['选择一', '选择二', '选择三', '选择四'], //导航栏格式 --导航栏组件
			tabBars: [],
			newsList: [],
			scrollViewInfo: "",
			currentPage: 0,
			currentTab: 0,
			enableScroll: true
		}
	},
	onLoad() {
		this.loadTabbars();
		setTimeout(() => {
			this.tabBars.forEach((tabBar) => {
				this.newsList.push({
					data: [],
					isLoading: false,
					refreshText: "",
					loadingText: '加载更多...'
				});
			});
			this.loadNewsList(0);
		}, 350)
	},
	methods: {
		//获取分类
		loadTabbars() {
			let tabList = json.mineTabList;
			// tabList.forEach(item => {
			// 	item.newsList = [];
			// 	item.loadMoreStatus = 0; //加载更多 0加载前，1加载中，2没有更多了
			// 	item.refreshing = 0;
			// })
			this.tabBars = tabList;
		},
		//获取新闻列表
		loadNewsList(index) {
			let activeTab = this.newsList[index];
			if(activeTab.data.length>0)return;
			setTimeout(() => {
				let list = [];
				for (let i = 0; i < 10; i++) {
					let item = json.newsList[Math.floor(Math.random() * json.newsList.length)];
					list.push(item);
				}
				activeTab.data = activeTab.data.concat(list);
			},1000)
			
		},


		onTabBarClick(e) { //单击导航栏时页面处理
			this.currentPage = this.currentTab = e;
			this.loadNewsList(e)
		},
		pageChange(e) { //滑动页面时页面处理
			let index = e.detail.current;
			this.currentPage = this.currentTab = index;
			this.loadNewsList(index);
		},
		loadMore(e) {
			console.log(e)
		}
	}
}
